data class PersonalityCategory(
    val name: String,
    val result: List<String>,
    val questions: List<PersonalityQuestion>
)

data class PersonalityQuestion(
    val text: String,
    val options: List<PersonalityOption>
)

data class PersonalityOption(
    val text: String,
    val value: Int
)

