package com.litecodez.soulscanner.components

import androidx.compose.runtime.Composable
import com.litecodez.soulscanner.pages.PersonalityProfile
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxSize
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.compose.ui.toAttrs
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun Profile(pp: PersonalityProfile){
    Column(
        verticalArrangement = Arrangement.Top,
        horizontalAlignment = Alignment.Start,
        modifier = Modifier.padding(10.px).fillMaxSize()
    ) {
        Row(){
            P(
                attrs = Modifier
                    .fontSize(25.px)
                    .toAttrs()
            ){
                Text("Your personality type is: ")
                Text(pp.type)
            }
        }
        Row(){
            P(
                attrs = Modifier
                    .fontSize(20.px)
                    .toAttrs()
            ){
                Text("Classification: ")
                Text(pp.classification)
            }

        }
        Row(){
            P(
                attrs = Modifier
                    .fontSize(18.px)
                    .toAttrs()
            ){
                Text("Work: ")
                Text(pp.work)
            }

        }
        Row(){
            P(
                attrs = Modifier
                    .fontSize(18.px)
                    .toAttrs()
            ){
                Text("Family: ")
                Text(pp.family)
            }

        }
        Row(){
            P(
                attrs = Modifier
                    .fontSize(18.px)
                    .toAttrs()
            ){
                Text("Intimate relationships: ")
                Text(pp.intimateRelationships)
            }
        }
    }
}